import _ from 'lodash'

export const calculateWeeklyTotal = (input) => {

    var monday = getMondayString(getMonday(new Date()));
    var a = _.filter(input, (o) => { return new Date((o.GameDate + ' UTC')) >= new Date(`${monday} 07:00 UTC`) });
    a = _.groupBy(a, "Name");
    a = _.map(a, (game, id) => ({
        Name: id,
        Profit: _.sumBy(game, item => Number(item.Profit)),
    }))
    a = _.orderBy(a, 'Profit', 'desc')
    return a
}


function getMondayString(inputDate){
    var day = inputDate.getDate().toString().padStart(2, '0');
    var month = (inputDate.getMonth() + 1).toString().padStart(2, '0'); // Since getMonth() returns month from 0-11 not 1-12
    var year = inputDate.getFullYear();
    return `${year}-${month}-${day}`;
}
function getMonday(d) {
  d = new Date(d);
  var day = d.getDay(),
      diff = d.getDate() - day + (day == 0 ? -6:1); // adjust when day is sunday
  return new Date(d.setDate(diff));
}

